import {arrayMove} from 'react-sortable-hoc';
import * as actionTypes from '../actions/types';

const initialState = {
    target_selector: "",
    src_wp: [],
    fetching_wp_status: "",
    src_wp_hash: [],
    src_uniconta: [],
    fetching_uniconta_status: "",
    src_uniconta_hash: [],
    new_line: {wp: [], uniconta: []},
    lines: [],
    mapping: "",
    src_wp_url: "",
    src_uniconta_url: "",
    focused: {index: null, key: null},
    multiple_wp: false,
    multiple_uniconta: false,
    inverse:false
};

/**
 * Set the target Element
 * @param state
 * @param action
 */
function set_target(state, action) {
    let selector = action.value;
    let newState = Object.assign({}, state, {target_selector: selector});
    // We ought to fetch any initial data from the target if any
    if (selector) {
        let targets = document.querySelectorAll(selector);
        let mapping = "";
        targets.forEach(function (target) {
            mapping = target.value;
            target.style.display = "none";
        });
        let lines = calculate_lines(mapping,state);
        console.log("lines target",lines);
        newState = Object.assign({}, newState, {mapping: mapping, lines: lines});
    }
    return newState;
}

/**
 * Setting inverse modifier
 * @param state
 * @param action
 */
function set_inverse(state, action){
    console.log("action inverse",action);
    return Object.assign({}, state, {inverse: action.value});
}

function set_multiple_modifiers(state, action) {
    return Object.assign({}, state, {multiple_wp: action.wp, multiple_uniconta: action.uniconta});
}

/**
 * Update a line on the list
 * @param state
 * @param action
 */
function update_line(state, action) {
    let key = action.key;
    let target = action.index;
    let value = action.value;
    let lines = state.lines.map((line, index) => {
        if (index !== target) return line;
        let new_line = Object.assign({}, line);

        if (typeof value === "string" || typeof value === "number") {
            value = [value];
            new_line[key] = new_line[key].concat(value);
        } else if (typeof value === "object") {
            new_line[key] = value;
        }


        switch (key) {
            case "wp":
                if (!state.multiple_wp && new_line[key].length !== 0) new_line[key] = [new_line[key].pop()];
                break;
            case "uniconta":
                if (!state.multiple_uniconta && new_line[key].length !== 0) new_line[key] = [new_line[key].pop()];
                break;
        }

        return new_line;
    });
    return _update_lines_and_mapping(state, lines);
}

/**
 * Update the position of a line after a drag n drop
 * @param state
 * @param action
 */
function update_line_pos(state, action) {
    let oldIndex = action.old;
    let newIndex = action.new;
    let lines = cloneObject(state.lines);

    let new_lines = arrayMove(lines, oldIndex, newIndex);
    return _update_lines_and_mapping(state, new_lines);
}

/**
 * Update the mapping on the textarea
 * @param state
 * @param action
 */
function update_mapping(state, action) {
    let mapping = action.value;
    update_target(state, mapping);
    return Object.assign({}, state, {mapping: mapping});
}

/**
 * Add a new line to the list
 * @param state
 * @param action
 * @returns {*}
 */
function add_new_line(state, action) {
    let new_line = cloneObject(state.new_line);
    let empty_line = {
        wp: [],
        uniconta: []
    };

    if (JSON.stringify(empty_line) === JSON.stringify(new_line)) {
        return state;
    }

    let lines = state.lines.concat(new_line);
    let new_state = _update_lines_and_mapping(state, lines);
    new_state = Object.assign({}, new_state, {new_line: empty_line});
    return new_state;
}

function remove_line(state, action) {
    let index = action.index;
    let lines = state.lines.filter((item, i) => {
        return i !== index;
    })
    return _update_lines_and_mapping(state, lines);
}

/**
 * Update the lines
 * @param state
 * @param action
 * @returns {*}
 */
function update_lines(state, action) {
    let lines = calculate_lines(action.value,state);
    return Object.assign({}, state, {lines: lines});
}

/**
 * Update new line
 * @param state
 * @param action
 * @returns {*}
 */
function update_new_line(state, action) {
    let key = action.key;
    let value = action.value;
    let new_line = Object.assign({}, state.new_line);

    if (typeof value === "string" || typeof value === "number") {
        value = [value];
        new_line[key] = new_line[key].concat(value);
    } else if (typeof value === "object") {
        new_line[key] = value;
    }

    switch (key) {
        case "wp":
            if (!state.multiple_wp && new_line[key].length !== 0) new_line[key] = [new_line[key].pop()];
            break;
        case "uniconta":
            if (!state.multiple_uniconta && new_line[key].length !== 0) new_line[key] = [new_line[key].pop()];
            break;
    }

    return Object.assign({}, state, {new_line: new_line});
}

/**
 * Helper function for updating the lines
 * @param lines
 * @private
 */
function _update_lines_and_mapping(state, lines) {
    let mapping = calculate_mapping(lines,state);
    update_target(state, mapping);
    return Object.assign({}, state, {mapping: mapping, lines: lines});
}

/**
 * Update the targets
 * @param state
 * @param mapping
 */
function update_target(state, mapping) {
    let targetSelector = state.target_selector;
    let targets = document.querySelectorAll(targetSelector);
    targets.forEach(function (target) {
        target.value = mapping;
    });
}

/**
 * Recalculate the lines from a mapping
 * @param mapping
 * @returns {Array}
 */
function calculate_lines(mapping,state) {
    // We calculate the lines from the mapping
    let lines = (mapping.trim() !== "") ? mapping.trim().split("\n") : []; // We split the mapping for each lines
    let result = [];

    let index_wp = 1;
    let index_uni = 0;

    console.log("state calculate lines",state);
    if(state.inverse){
        index_wp = 0;
        index_uni = 1;
    }

    console.log("state",state);

    if(lines.length > 0){
        result = lines.map((value, index) => {
            if (value.trim() === "") return;
            let data = value.split("|");

            let uniconta = (typeof data[index_uni] !== 'undefined') ? data[index_uni] : "";
            let wp = (typeof data[index_wp] !== 'undefined') ? data[index_wp] : "";

            return ({
                wp: wp.split(","),
                uniconta: uniconta.split(",")
            });
        });
    }

    return result;
}

/**
 * Recalculate a mapping from the lines data
 * @param lines
 */
function calculate_mapping(lines,state) {
    let values = [];
    lines.map((value, index) => {
        let wp = value.wp.join(",").trim();
        let uniconta = value.uniconta.join(",").trim();
        if (wp.length === 0 && uniconta.length === 0) return "";

        if(state.inverse){
            values.push(wp + "|" + uniconta);
        }else{
            values.push(uniconta + "|" + wp);
        }
    });
    return values.join("\n");
}

/**
 * Fetch the data from an url
 * @param state
 * @param action
 */
function fetch_src_wp(state, action) {
    let data = action.data;
    // data = [{"title": "stock", "value": "stock"}, {"title": "post_title", "value": "post_title"}, {
    //     "title": "category",
    //     "value": "10"
    // }];
    let hash = data.map((item, index) => {
        return item.value;
    });
    return Object.assign({}, state, {src_wp_hash: hash, src_wp: data});
}

/**
 * Setting the status of the fetching of the wp fields
 * @param state
 * @param action
 */
function fetching_src_wp_status(state,action){
    return Object.assign({}, state, {fetching_wp_status:action.data});
}

/**
 * Fetch the data from an url
 * @param state
 * @param action
 */
function fetch_src_uniconta(state, action) {
    let data = action.data;
    // data = [{"title": "_Qty", "value": "_Qty"}, {"title": "ProduktNavn", "value": "ProduktNavn"}];
    let hash = data.map((item, index) => {
        return item.value;
    });
    return Object.assign({}, state, {src_uniconta_hash: hash, src_uniconta: data});
}

/**
 * Setting the status of the fetching of the wp fields
 * @param state
 * @param action
 */
function fetching_src_uniconta_status(state,action){
    return Object.assign({}, state, {fetching_uniconta_status:action.data});
}

/**
 * Set which input has been focused
 * @param state
 * @param action
 */
function set_focused_input(state, action) {
    return Object.assign({}, state, {focused: {index: action.index, key: action.key}});
}

/**
 * Fill the focused input with a value
 * @param state
 * @param action
 */
function fill_focused_input(state, action) {
    let focused = state.focused;

    let index = focused.index;
    let key = action.key;
    let tag = action.value;

    action.index = index;
    // action.key = key;
    action.value = tag.value;

    console.log("state/action", state,action);

    if (index >= 0) {
        return update_line(state, action);
    } else {
        return update_new_line(state, action);
    }
}

export function cloneObject(obj) {
    return JSON.parse(JSON.stringify(obj));
}

export default function reducer(state = initialState, action) {
    // console.log("action",action);
    switch (action.type) {
        case actionTypes.SET_TARGET:
            return set_target(state, action);
        case actionTypes.SET_INVERSE:
            return set_inverse(state, action);
        case actionTypes.SET_MULTIPLE_MODIFIERS:
            return set_multiple_modifiers(state, action);
        case actionTypes.ADD_NEW_LINE:
            return add_new_line(state, action);
        case actionTypes.REMOVE_LINE:
            return remove_line(state, action);
        case actionTypes.UPDATE_LINE:
            return update_line(state, action);
        case actionTypes.UPDATE_LINE_POS:
            return update_line_pos(state, action);
        case actionTypes.UPDATE_LINES:
            return update_lines(state, action)
        case actionTypes.UPDATE_NEW_LINE:
            return update_new_line(state, action);
        case actionTypes.UPDATE_MAPPING:
            return update_mapping(state, action);
        case actionTypes.FETCH_SRC_WP:
            return fetch_src_wp(state, action);
        case actionTypes.FETCHING_SRC_WP_STATUS:
            return fetching_src_wp_status(state, action);
        case actionTypes.FETCH_SRC_UNICONTA:
            return fetch_src_uniconta(state, action);
        case actionTypes.FETCHING_SRC_UNICONTA_STATUS:
            return fetching_src_uniconta_status(state, action);
        case actionTypes.UPDATE_FOCUSED_INPUT:
            return set_focused_input(state, action);
        case actionTypes.FILL_FOCUSED_INPUT:
            return fill_focused_input(state, action);
        default:
            return state;
    }
}


