import React, { Component } from 'react';
import Wedoio_mapper from './wedoio-mapper/wedoio-mapper';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
          <Wedoio_mapper
              original={this.props.original}
              src_wp_url={this.props.src_wp_url}
              src_uniconta_url={this.props.src_uniconta_url}
              multiple_wp_values={this.props.multiple_wp_values}
              multiple_uniconta_values={this.props.multiple_uniconta_values}
              inverse={this.props.inverse}
          />
      </div>
    );
  }
}

export default App;



