import React, {Component} from 'react';
import {SortableHandle} from 'react-sortable-hoc';
import TagsInput from 'react-tagsinput';
import {Scrollbars} from 'react-custom-scrollbars';
import 'react-tagsinput/react-tagsinput.css';


const DragHandle = SortableHandle(() => <span className="drag-handle"><i className="icon-arrows"/></span>);

class Line extends Component {

    constructor(props) {
        super(props);


        const wp = this.props.wp.join(",");
        const uniconta = this.props.uniconta.join(",");

        this.state = {
            wp: wp.trim() !== "" ? wp.trim() : [],
            uniconta: uniconta.trim() !== "" ? uniconta.trim() : [],
        };

        this.wp_ref = React.createRef();
        this.uniconta_ref = React.createRef();
    }

    updateWp = (e) => {
        this.props.onWpUpdate(e);
    }

    updateUniconta = (e) => {
        this.props.onUnicontaUpdate(e);
    }

    addNewLine = (e) => {
        this.props.onLineAdd(e);
    }

    removeLine = (e) => {
        this.props.onLineRemove(e);
    }

    inputFocused = (e) => {
        this.props.onLineFocused(e);
    }

    renderTag = (props, src_key) => {
        let {tag, key, disabled, onRemove, classNameRemove, getTagDisplayValue, ...other} = props
        return (
            <span key={key} {...other}>
                {this.getTagDisplayValue(src_key,tag)}
                {!disabled &&
                <a className={classNameRemove} onClick={(e) => onRemove(key)}/>
                }
            </span>
        )
    }

    getTagDisplayValue(key,tag){
        let src = [];

        switch(key){
            case "wp":
                src = this.props.src_wp;
                break;
            case "uniconta":
                src = this.props.src_uniconta;
                break;
        }

        if(typeof tag !== "undefined"){
            tag = tag.toString();
            let matches = src.filter((item) => {
                return typeof tag !== undefined && tag.toLowerCase() === item.value.toString().toLowerCase();
            });

            if(matches.length !== 0) {
                tag = matches.pop().title;
            }
        }

        return tag;
    }

    render() {
        const wp = this.props.wp;
        const uniconta = this.props.uniconta;

        return (
            <div className="wedoio-mapper-line">
                {this.props.rmvButton &&
                <DragHandle/>}

                <input value={uniconta} onChange={this.updateUniconta} style={{display: "none"}}/>
                <input value={wp} onChange={this.updateWp} style={{display: "none"}}/>

                <Scrollbars className="scroll-container" autoHeight autoHeightMin={35} autohide autoHideTimeout={1000}
                            autoHideDuration={200}
                            style={{width: "50%", display: "inline-block", overflowY: "hidden"}}>
                    <TagsInput value={wp} onChange={this.updateWp}
                               inputProps={{placeholder: "", onFocus: () => this.inputFocused("wp")}}
                               renderTag={(props) => this.renderTag(props,"wp")} />
                </Scrollbars>

                <Scrollbars className="scroll-container" autoHeight autoHeightMin={35} autohide autoHideTimeout={1000}
                            autoHideDuration={200}
                            style={{width: "50%", display: "inline-block", overflowY: "hidden"}}>
                    <TagsInput value={uniconta} onChange={this.updateUniconta}
                               inputProps={{placeholder: "", onFocus: () => this.inputFocused("uniconta")}}
                               renderTag={(props) => this.renderTag(props,"uniconta")} />
                </Scrollbars>

                {this.props.addButton &&
                <span className="btn add-btn" onClick={(e) => {
                    this.addNewLine()
                }}><i className="icon-plus"/></span>}
                {this.props.rmvButton &&
                <span className="btn rmv-btn" onClick={(e) => {
                    this.removeLine()
                }}><i className="icon-times"/></span>}
            </div>
        );
    }
}

export default Line;


