import * as actionTypes from './types';


export function set_target(selector){
    return{
        type:actionTypes.SET_TARGET,
        value:selector
    }
}

export function set_inverse(inverse){
    return{
        type:actionTypes.SET_INVERSE,
        value:inverse
    }
}

export function set_multiple_modifiers(multiple_wp,multiple_uniconta){
    return{
        type:actionTypes.SET_MULTIPLE_MODIFIERS,
        wp:multiple_wp,
        uniconta:multiple_uniconta
    }
}

export function update_mapping(mapping){
    return{
        type:actionTypes.UPDATE_MAPPING,
        value:mapping
    }
}

export function add_new_line(){
    return{
        type:actionTypes.ADD_NEW_LINE
    };
}

export function update_new_line(value,key){
    return{
        type:actionTypes.UPDATE_NEW_LINE,
        key:key,
        value:value
    };
}

export function remove_line(index){
    return{
        type:actionTypes.REMOVE_LINE,
        index:index
    };
}

export function update_line(value,key,index){
    return{
        type:actionTypes.UPDATE_LINE,
        key:key,
        value:value,
        index:index
    }
}

export function update_line_pos(old_index,new_index){
    return{
        type:actionTypes.UPDATE_LINE_POS,
        old:old_index,
        new:new_index
    }
}

export function update_lines(mapping){
    return{
        type:actionTypes.UPDATE_LINES,
        value:mapping
    }
}

export function fetch_src_wp(data){
    return{
        type:actionTypes.FETCH_SRC_WP,
        data:data
    }
}

export function fetching_src_wp_status(data){
    return{
        type:actionTypes.FETCHING_SRC_WP_STATUS,
        data:data
    }
}

export function fetch_src_uniconta(data){
    return{
        type:actionTypes.FETCH_SRC_UNICONTA,
        data:data
    }
}

export function fetching_src_uniconta_status(data){
    return{
        type:actionTypes.FETCHING_SRC_UNICONTA_STATUS,
        data:data
    }
}

export function update_focused_input(index,key){
    return{
        type:actionTypes.UPDATE_FOCUSED_INPUT,
        index:index,
        key:key
    }
}

export function fill_focused_input(value,key){
    return{
        type:actionTypes.FILL_FOCUSED_INPUT,
        value:value,
        key:key
    }
}


