import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {Provider} from 'react-redux';
import {createStore} from 'redux';
import reducer from './wedoio-mapper/reducers';
import registerServiceWorker from './registerServiceWorker';

let roots = document.querySelectorAll(".root-mapper");

if (roots.length >= 0) {
    roots.forEach((root) => {
        let original = root.getAttribute("data-original");

        let src_wp_url = root.getAttribute("data-src-wp-url");
        let src_uniconta_url = root.getAttribute("data-src-uniconta-url");

        let multiple_wp_values = root.getAttribute("data-multiple-wp") !== null;
        let multiple_uniconta_values = root.getAttribute("data-multiple-uniconta") !== null;

        let inverse = root.getAttribute("data-inverse") !== null;

        const store = createStore(reducer);

        ReactDOM.render(
            <Provider store={store}>
                <App original={original}
                     src_wp_url={src_wp_url}
                     src_uniconta_url={src_uniconta_url}
                     multiple_wp_values={multiple_wp_values}
                     multiple_uniconta_values={multiple_uniconta_values}
                     inverse={inverse}/>
            </Provider>, root);

        //registerServiceWorker();
    });
}



