import React, {Component} from 'react';
import {connect} from 'react-redux';
import {
    update_mapping, set_target, update_lines, update_line, update_new_line, add_new_line,
    remove_line, update_line_pos, fetch_src_wp, fetch_src_uniconta, update_focused_input, fill_focused_input,
    set_multiple_modifiers, fetching_src_wp_status, fetching_src_uniconta_status, set_inverse
} from "./actions/index";
import {SortableContainer, SortableElement} from 'react-sortable-hoc';
import {Scrollbars} from 'react-custom-scrollbars';
import Line from "./line";
import axios from 'axios';
import {woo_logo, uni_logo} from "./logos";

const SortableItem = SortableElement(({line, element, id}) => {
    return (
        <Line wp={line.wp} uniconta={line.uniconta}
              rmvButton
              onWpUpdate={(e) => {
                  element.onWpUpdate(e, id);
              }}
              onUnicontaUpdate={(e) => {
                  element.onUnicontaUpdate(e, id);
              }}
              onLineRemove={(e) => {
                  element.onLineRemove(id);
              }}
              onLineFocused={(e) => {
                  if (typeof e === "string") element.onLineFocused(id, e)
              }}
              src_wp={element.props.src_wp} src_uniconta={element.props.src_uniconta}
              src_wp_hash={element.props.src_wp_hash} src_uniconta_hash={element.props.src_uniconta_hash}
        />);
});

const SortableList = SortableContainer(({lines, element}) => {
    return (<div className="wedoio-mapper-container">
        {
            lines.map((line, index) => {
                return (<SortableItem key={`line-${index}`} index={index} line={line} element={element} id={index}/>)
            })
        }
    </div>);
});

class Core extends Component {

    constructor(props){
        super(props);
        this.props.dispatch(set_inverse(this.props.inverse));
        this.props.dispatch(set_target(this.props.original));
    }

    componentDidMount() {

        this.fetchSrc();
        // We set the multiple modifiers
        this.props.dispatch(set_multiple_modifiers(this.props.multiple_wp_values, this.props.multiple_uniconta_values));
    }

    onMappingChange = (e) => {
        this.props.dispatch(update_mapping(e.target.value));
        this.props.dispatch(update_lines(e.target.value));
    }

    onWpUpdate = (e, i) => {
        this.props.dispatch(update_line(e, 'wp', i));
    }

    onUnicontaUpdate = (e, i) => {
        this.props.dispatch(update_line(e, 'uniconta', i));
    }

    onNewLineUpdate = (e, key) => {
        console.log("uniconta", e, key);
        this.props.dispatch(update_new_line(e, key));
    }

    onNewLineAdd = (e) => {
        this.props.dispatch(add_new_line());
    }

    onLineRemove = (i) => {
        this.props.dispatch(remove_line(i));
    }

    onSortEnd = ({oldIndex, newIndex}) => {
        this.props.dispatch(update_line_pos(oldIndex, newIndex));
    }

    onLineFocused = (index, key) => {
        this.props.dispatch(update_focused_input(index, key));
    }

    onTagClicked = (value, key) => {
        this.props.dispatch(fill_focused_input(value, key));
    }

    fetchSrc = () => {

        let wp_url = this.props.src_wp_url;
        let uniconta_url = this.props.src_uniconta_url;

        if (wp_url) {
            this.props.dispatch(fetching_src_wp_status("fetching ..."));
            wp_url = wp_url.replace("[window.ajax]", window.ajaxurl);
            axios.get(wp_url).then(
                (res) => {
                    let data = res.data;
                    this.props.dispatch(fetching_src_wp_status(""));
                    this.props.dispatch(fetch_src_wp(data));
                },
                (err) => {
                    this.props.dispatch(fetching_src_wp_status("error while fetching wp fields ..."));
                    console.log(err);
                });
        }

        if (uniconta_url) {
            this.props.dispatch(fetching_src_uniconta_status("fetching ..."));
            uniconta_url = uniconta_url.replace("[window.ajax]", window.ajaxurl);
            axios.get(uniconta_url).then(
                (res) => {
                    let data = res.data;
                    this.props.dispatch(fetching_src_uniconta_status(""));
                    this.props.dispatch(fetch_src_uniconta(data));
                },
                (err) => {
                    this.props.dispatch(fetching_src_uniconta_status("error while fetching uniconta fields"));
                    console.log("err", err);
                });
        }

    }

    render() {

        return (
            <div className="wedoio-mapper-core">
                <div className="wedoio-mapper-add-container">
                    <h3 className="column-title"><img src={woo_logo} height={30}/></h3>
                    <h3 className="column-title"><img src={uni_logo} height={20}
                                                      style={{position: "relative", top: "0px"}}/></h3>
                    <Line wp={this.props.new_line.wp}
                          uniconta={this.props.new_line.uniconta}
                          onWpUpdate={(e) => {
                              this.onNewLineUpdate(e, "wp")
                          }} onUnicontaUpdate={(e) => {
                        this.onNewLineUpdate(e, "uniconta")
                    }} onLineAdd={this.onNewLineAdd} src_wp={this.props.src_wp} src_uniconta={this.props.src_uniconta}
                          src_wp_hash={this.props.src_wp_hash}
                          src_uniconta_hash={this.props.src_uniconta_hash}
                          addButton
                          onLineFocused={(e) => {
                              if (typeof e === "string") this.onLineFocused(-1, e)
                          }}/>
                </div>

                {this.props.lines.length > 0 &&
                <SortableList lines={this.props.lines} element={this} useDragHandle={true} onSortEnd={this.onSortEnd}
                              lockAxis={"y"}/>
                }

                <textarea rows="10" value={this.props.mapping} onChange={this.onMappingChange}
                          style={{display: "none"}}/>
                <div className="wedoio-mapper-boxes">

                    <div className="wedoio-mapper-suggestion-box">
                        <div className="info">
                            <div className="info-img">
                                <img src={woo_logo} height={30}/>
                            </div>
                            <p>Click on an input then choose a tag to copy its value</p>
                        </div>
                        <div className="fields">
                            {this.props.fetching_wp_status !== "" &&
                            <div>
                                <span className="rotate-animation"><i className="icon-refresh"></i></span>
                                <span>{this.props.fetching_wp_status}</span>
                            </div>
                            }
                            <Scrollbars className="scroll-container" autoHeight autoHeightMin={0} autoHeightMax={310}
                                        autohide
                                        autoHideTimeout={1000}>

                                {
                                    this.props.src_wp.map((value, index) => {
                                        return (<span key={`wp-s-${index}`} className="suggestion-tag"
                                                      value={value.value}
                                                      onClick={(e) => {
                                                          this.onTagClicked(value, "wp");
                                                      }}>{value.title}</span>);
                                    })
                                }

                            </Scrollbars>
                        </div>
                    </div>

                    <div className="wedoio-mapper-suggestion-box">
                        <div className="info">
                            <div className="info-img">
                                <img src={uni_logo} height={20}/>
                            </div>
                            <p>Click on an input then choose a tag to copy its value</p>
                        </div>
                        <div className="fields">
                            {this.props.fetching_uniconta_status != "" &&
                            <div>
                                <span className="rotate-animation"><i className="icon-refresh"></i></span>
                                <span>{this.props.fetching_uniconta_status}</span>
                            </div>}
                            <Scrollbars className="scroll-container" autoHeight autoHeightMin={0} autoHeightMax={310}
                                        autohide
                                        autoHideTimeout={1000}>
                                {
                                    this.props.src_uniconta.map((value, index) => {
                                        return (<span key={`uni-s-${index}`}
                                                      className="suggestion-tag"
                                                      value={value.value}
                                                      onClick={(e) => {
                                                          this.onTagClicked(value, "uniconta");
                                                      }}>{value.title}</span>);
                                    })
                                }
                            </Scrollbars>
                        </div>
                    </div>

                </div>

            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        src_wp: state.src_wp,
        fetching_wp_status: state.fetching_wp_status,
        src_wp_hash: state.src_wp_hash,
        src_uniconta: state.src_uniconta,
        fetching_uniconta_status: state.fetching_uniconta_status,
        src_uniconta_hash: state.src_uniconta_hash,
        new_line: state.new_line,
        lines: state.lines,
        mapping: state.mapping,
        focused: state.focused
    }
}

export default connect(mapStateToProps)(Core);


